<template>
  <div class="otp-input" :class="[{ divided: divider }, props.class]" :style="props.style">
    <div ref="content" class="otp-content" :style="dimensionStyles">
      <template v-for="(_, i) in fields" :key="i">
        <span v-if="divider && i !== 0" class="input-divider">{{ divider }}</span>
        
        <div class="field">
          <input
            :ref="(el) => setInputRef(el, i)"
            :aria-label="label"
            :autofocus="i === 0 && autofocus"
            autocomplete="one-time-code"
            class="input-field"
            :class="[inputClass, conditionalClasses[i]]"
            :disabled="disabled"
            :inputmode="type === 'number' ? 'numeric' : 'text'"
            :min="props.type === 'number' ? 0 : undefined"
            maxlength="1"
            :placeholder="Array.isArray(placeholder) ? (placeholder[i] ?? '') : placeholder"
            :type="type === 'number' ? 'text' : type"
            :value="model ? model[i] ?? '' : ''"
            @input="onInput"
            @focus="onFocus($event, i)"
            @blur="onBlur"
            @keydown="onKeydown"
            @paste="onPaste($event, i)"
          />
        </div>
      </template>

      <input class="input-input" type="hidden" :maxlength="length" :value="model.join('')" />
    </div>
  </div>
</template>

<script setup>
  import { ref, computed, defineProps, defineEmits, watch, nextTick } from 'vue';

  const props = defineProps({
    autofocus: {
      type: Boolean,
      default: false
    },
    class: {
      type: [String, Array],
      default: ''
    },
    conditionalClasses: {
      type: Array,
      default: () => ([])
    },
    disabled: {
      type: Boolean,
      default: false
    },
    divider: {
      type: String,
      default: ''
    },
    error: {
      type: Boolean,
      default: false
    },
    focusAll: {
      type: Boolean,
      default: false
    },
    focused: {
      type: Boolean,
      default: false
    },
    height: {
      type: [Number, String],
      default: ''
    },
    inputClass: {
      type: [String, Array],
      default: ''
    },
    label: {
      type: String,
      default: ''
    },
    length: {
      type: [Number, String],
      default: 6
    },
    // loading: {
    //   type: [Boolean, String],
    //   default: false
    // },
    maxHeight: {
      type: [Number, String],
      default: ''
    },
    maxWidth: {
      type: [Number, String],
      default: ''
    },
    minHeight: {
      type: [Number, String],
      default: ''
    },
    minWidth: {
      type: [Number, String],
      default: ''
    },
    modelValue: {
      type: [Number, String],
      default: ''
    },
    placeholder: {
      type: [String, Array],
      default: ''
    },
    type: {
      type: String,
      default: 'number',
      validator: (v) => ['text', 'password', 'number'].includes(v)
    },
    style: {
      type: [String, Array, Object],
      default: null
    },
    width: {
      type: [Number, String],
      default: ''
    }
  });

  const emit = defineEmits(['finish', 'update:focused', 'update:modelValue']);

  const content = ref();
  const input = ref(Array.from({ length: Number(props.length) }));
  const isFocused = ref(props.focused ?? false);

  const current = computed(() => {
    if (input.value) {
      return input.value[focusIndex.value];
    } else {
      return undefined;
    }
  });

  const dimensionStyles = computed(() => ({
    height: convertToUnit(props.height),
    maxHeight: convertToUnit(props.maxHeight),
    maxWidth: convertToUnit(props.maxWidth),
    minHeight: convertToUnit(props.minHeight),
    minWidth: convertToUnit(props.minWidth),
    width: convertToUnit(props.width)
  }));

  const fields = computed(() => (Array.from({ length: length.value }).fill(0)));
  const focusIndex = ref(-1);
  const length = computed(() => (Number(props.length)));

  const model = computed(() => {
    if (props.modelValue) {
      return String(props.modelValue).split('');
    } else {
      return [];
    }
  });

  watch(() => props.focused, () => {
    if (props.focused !== isFocused.value) {
      isFocused.value = props.focused;
    }
  });

  watch(focusIndex, (val) => {
    if (val < 0) {
      return;
    }

    nextTick(() => {
      if (input.value && input.value[val]) {
        input.value[val].select();
      }
    });
  });

  watch(length, () => {
    if (input.value) {
      if (input.value.length < length.value) {
        input.value = input.value.concat(Array.from({ length: length.value - input.value.length }));
      } else if (input.value.length > length.value) {
        input.value.splice(0, length.value);
      }
    }
  });

  watch(model, (val) => {
    if (val.length === length.value) {
      emit('finish', val.join(''));
    }
  }, { deep: true });

  function blur() {
    isFocused.value = false;
    emit('update:focused', isFocused.value);
  }

  function convertToUnit(str, unit = 'px') {
    if (str === null || str === '') {
      return undefined;
    } else if (Number.isNaN(+str ?? '')) {
      return String(str);
    } else if (!Number.isFinite(+str ?? '')) {
      return undefined;
    } else {
      return `${Number(str)}${unit}`;
    }
  }

  function focus() {
    isFocused.value = true;
    emit('update:focused', isFocused.value);
  }

  function focusChild(el, location) {
    const focusable = focusableChildren(el);

    if (!location) {
      if (el === document.activeElement || !el.contains(document.activeElement)) {
        if (focusable && focusable[0]) {
          focusable[0].focus();
        }
      }
    } else if (location === 'first') {
      if (focusable && focusable[0]) {
        focusable[0].focus();
      }
    } else if (location === 'last') {
      if (focusable && focusable.at(-1)) {
        focusable.at(-1).focus();
      }
    } else if (typeof location === 'number') {
      if (focusable && focusable[location]) {
        focusable[location].focus();
      }
    } else {
      const elem = getNextElement(focusable, location);
      if (elem) {
        elem.focus();
      } else {
        focusChild(el, location === 'next' ? 'first' : 'last');
      }
    }
  }

  function focusableChildren(el, filterByTabIndex = true) {
    const targets = ['button', '[href]', 'input:not([type="hidden"])', 'select', 'textarea', '[tabindex]']
      .map((s) => `${s}${filterByTabIndex ? ':not([tabindex="-1"])' : ''}:not([disabled])`)
      .join(', ');
    return [...el.querySelectorAll(targets)];
  }

  function getNextElement(elements, location, condition) {
    let el;
    let idx = elements.indexOf(document.activeElement);
    const inc = location === 'next' ? 1 : -1;
    do {
      idx += inc;
      el = elements[idx];
    } while ((!el || el.offsetParent == null || !(condition?.(el) ?? true)) && idx < elements.length && idx >= 0);
    return el;
  }

  function isValidNumber(value) {
    return props.type === 'number' && /[^0-9]/g.test(value);
  }

  function onFocus(e, index) {
    focus();

    focusIndex.value = index;
  }

  function onBlur() {
    blur();

    focusIndex.value = -1;
  }

  function onInput() {
    // The maxlength attribute doesn't work for the number type input, so the text type is used.
    // The following logic simulates the behaviour of a number input.
    if (isValidNumber(current.value.value)) {
      current.value.value = '';
      return;
    }

    const array = model.value.slice();
    const value = current.value.value;

    array[focusIndex.value] = value;

    let target;

    if (focusIndex.value > model.value.length) {
      target = model.value.length + 1;
    } else if (focusIndex.value + 1 !== length.value) {
      target = 'next';
    }

    emit('update:modelValue', array.join(''));

    if (target) {
      focusChild(content.value, target);
    }
  }

  function onKeydown(e) {
    const array = Object.assign([], model.value);
    const index = focusIndex.value;
    let target = null;

    if (!['ArrowLeft', 'ArrowRight', 'Backspace', 'Delete'].includes(e.key)) {
      return;
    }

    e.preventDefault();

    if (e.key === 'ArrowLeft') {
      target = 'prev';
    } else if (e.key === 'ArrowRight') {
      target = 'next';
    } else if (['Backspace', 'Delete'].includes(e.key)) {
      array[focusIndex.value] = '';
      emit('update:modelValue', array.join(''));

      if (focusIndex.value > 0 && e.key === 'Backspace') {
        target = 'prev';
      } else {
        requestAnimationFrame(() => {
          if (input.value && input.value[index]) {
            input.value[index].select();
          }
        });
      }
    }

    requestAnimationFrame(() => {
      if (target !== null) {
        focusChild(content.value, target);
      }
    });
  }

  function onPaste(e, i) {
    e.preventDefault();
    e.stopPropagation();
    
    const clipboardText = e?.clipboardData?.getData('Text')?.slice(0, length.value) ?? '';

    if (isValidNumber(clipboardText)) {
      return;
    }

    emit('update:modelValue', clipboardText);

    if (input.value) {
      input.value[i].blur();
    }
  }

  function reset() {
    emit('update:modelValue', '');
  }

  function setInputRef(el, idx) {
    if (el) {
      input.value[idx] = el;
    }
  }
</script>

<style lang="scss" scoped>
  .otp-input {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
    border-radius: 4px;
    background-repeat: no-repeat;
    box-sizing: inherit;
    position: relative;

    * {
      padding: 0;
      margin: 0;
    }

    *, ::before, ::after {
      background-repeat: no-repeat;
      box-sizing: inherit;
    }

    .otp-content {
      display: flex;
      align-items: center;
      justify-content: center;
      // gap: 0.5rem;
      min-height: 2.8rem;
      // max-width: 320px;
      // padding: 0.5rem;
      border-radius: inherit;
      position: relative;
    }

    .field {
      --field-padding-start: 16px;
      --field-padding-end: 16px;
      --field-padding-top: 0;
      --field-padding-bottom: 4px;
      --field-input-padding-top: calc(var(--field-padding-top, 8px) + var(--input-padding-top, 0));
      --field-input-padding-bottom: var(--field-padding-bottom, 4px);

      display: flex;
      height: 100%;
      max-width: 100%;
      flex: 1 0;
      contain: layout;
      font-size: 16px;
      letter-spacing: 0.009375em;
      position: relative;
    }

    .input-divider {
      margin: 0 8px;
    }

    .input-field {
      width: 100%;
      height: 100%;
      outline: none;
      font-size: 1.25rem;
      text-align: center;
      color: inherit;
    }

    input {
      background-color: transparent;
      border-style: none;
      border-radius: 0;
      font: inherit;
    }
  }
</style>
