<template>
  <div class="file-input" :class="classes" :style="style">
    <div v-if="prependIcon && prepend === 'outer'" class="prepend" @click="onClickPrepend">
      <i :class="prependIcon" />
    </div>
    <div class="control" @click="onControlClick" @mousedown="onControlMousedown">
      <div class="field" :class="{ 'prepend-inner': prependIcon && prepend === 'inner', 'append-inner': appendIcon && append === 'inner' }">
        <!-- <div class="field-overlay"></div> -->
        <div class="field-loader"></div>
        <div v-if="prependIcon && prepend === 'inner'" class="field-prepend-inner">
          <i :class="prependIcon" />
        </div>
        <div class="field-content">
          <label class="field-label" aria-hidden="true" :for="id">{{ label }}</label>
          <label class="field-label floating" :for="id">{{ label }}</label>
          <input
            ref="input"
            type="file"
            :id="id"
            :accept="accept"
            :disabled="disabled"
            :readonly="readonly"
            :multiple="multiple"
            :name="name"
            :placeholder="placeholder"
            :aria-describedby="`${id}-messages`"
            @change="handleFileChange"
            @click="handleClick"
            @focus="onFocus"
            @blur="blur" />
            <div class="field-input">
              <slot name="selection" :fileNames="fileNames" :totalBytes="totalBytes" :totalBytesReadable="totalBytesReadable">
                <template v-if="chips">
                  <Chip v-for="text in fileNames" :key="text" :label="text" />
                </template>
                <template v-else>
                  {{ fileNames.join(', ') }}
                </template>
              </slot>
            </div>
        </div>
        <div class="field-clearable">
          <i :class="clearIcon" @click="onClear" />
        </div>
        <div v-if="appendIcon && append === 'inner'" class="field-append-inner">
          <i :class="appendIcon" />
        </div>
        <!-- <div class="outline"></div> -->
      </div>
    </div>
    <div v-if="appendIcon && append === 'outer'" class="append">
      <i :class="appendIcon" />
    </div>
    <div class="details">
      <TransitionGroup name="messages" tag="div" class="messages" :class="{ active: fieldMessages.length }" role="alert" aria-live="polite" :id="`${id}-messages`">
          <div v-for="(message, index) in fieldMessages" :key="`${index}-${message}`" class="message">{{ message }}</div>
      </TransitionGroup>
      <!--  -->
      <template v-if="props.counter">
        <span />
        <div class="counter" :class="{ visible: fileNames.length }">{{ counterValue }}</div>
      </template>
    </div>
  </div>
</template>

<script setup>
  import { ref, computed, shallowRef, watch, defineProps, defineEmits, nextTick, onMounted } from 'vue';
  import { useToggleScope } from '@/utilities/ProxiedModel';
  import wrapInArray from '@/utilities/wrapInArray';

  const props = defineProps({
    accept: {
      type: String,
      default: ''
    },
    active: {
      type: Boolean,
      default: false
    },
    append: {
      type: String,
      default: 'outer',
      validator: (v) => ['inner', 'outer'].includes(v)
    },
    appendIcon: {
      type: String,
      default: ''
    },
    chips: {
      type: Boolean,
      default: false
    },
    class: {
      type: [String, Array],
      default: ''
    },
    clearable: {
      type: Boolean,
      default: false
    },
    clearIcon: {
      type: String,
      default: 'pi pi-times-circle'
    },
    counter: {
      type: Boolean,
      default: false
    },
    dirty: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    error: {
      type: Boolean,
      default: false
    },
    errorMessages: {
      type: [Array, String],
      default: () => []
    },
    focused: {
      type: Boolean,
      default: false
    },
    hideDetails: {
      type: [Boolean, String],
      default: 'auto',
      validator: (v) => typeof v === 'boolean' || v === 'auto'
    },
    hint: {
      type: String,
      default: ''
    },
    id: {
      type: String
    },
    label: {
      type: String,
      default: 'File input'
    },
    loading: {
      type: [Boolean, String],
      default: false
    },
    maxErrors: {
      type: [Number, String],
      default: 1
    },
    messages: {
      type: [Array, String],
      default: () => []
    },
    modelValue: {
      type: [FileList, undefined]
    },
    multiple: {
      type: Boolean,
      default: false
    },
    name: {
      type: String,
      default: ''
    },
    persistentClear: {
      type: Boolean,
      default: false
    },
    persistentHint: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    prepend: {
      type: String,
      default: 'outer',
      validator: (v) => ['inner', 'outer'].includes(v)
    },
    prependIcon: {
      type: String,
      default: 'pi pi-paperclip'
    },
    readonly: {
      type: Boolean,
      default: false
    },
    rules: {
      type: Array,
      default: () => []
    },
    showSize: {
      type: [Boolean, Number, String],
      default: false,
      validator: (v) => typeof v === 'boolean' || [1000, 1024].includes(Number(v))
    },
    style: {
      type: [String, Array, Object],
      default: null
    },
    validateOn: {
      type: String,
      default: ''
    },
    validationValue: {
      type: [Array, Object],
      default: () => [],
      validator: (val) => wrapInArray(val).every((v) => v !== null && typeof v === 'object')
    },
  });

  const emit = defineEmits([
    'click:control',
    'mousedown:control',
    'onClick:clear',
    'update:focused',
    'update:errors',
    'update:modelValue'
  ]);

  const input = ref();
  const internalErrorMessages = ref([]);
  // const errors = ref([]);
  const totalBytes = ref(0);

  const isFocused = ref(props.focused ?? false);
  const isPristine = shallowRef(true);
  const isValidating = shallowRef(false);

  const base = computed(() => {
    return typeof props.showSize !== 'boolean' ? props.showSize : undefined;
  });

  const classes = computed(() => {
    let output = [];

    if (props.prependIcon && props.prepend === 'outer') {
      output.push('prepend');
    }

    if (props.appendIcon && props.append === 'outer') {
      output.push('append');
    }

    if (!isValid.value) {
      output.push('error');
    }

    if (isActive.value) {
      output.push('active');
    }

    if (isFocused.value) {
      output.push('focused');
    }

    if (isDirty.value) {
      output.push('dirty');
    }

    if (props.disabled) {
      output.push('disabled');
    }

    if (props.readonly) {
      output.push('readonly');
    }
    
    if (props.chips) {
      output.push('chips');
    }
    
    if (Array.isArray(props.class)) {
      output = output.concat(props.class);
    } else {
      output.push(props.class);
    }

    return output;
  });

  const counterValue = computed(() => {
    const fileCount = model.value?.length ?? 0;
    if (props.showSize) {
      return `${fileCount} ${fileCount > 1 ? 'files' : 'file'} (${totalBytesReadable.value} in total)`;
    } else {
      return `${fileCount} ${fileCount > 1 ? 'files' : 'file'}`;
    }
  });

  const errorMessages = computed(() => {
    return props.errorMessages?.length
      ? wrapInArray(props.errorMessages).concat(internalErrorMessages.value).slice(0, Math.max(0, +props.maxErrors))
      : internalErrorMessages.value;
  });

  const fieldMessages = computed(() => {
    if (props.errorMessages?.length || (!isPristine.value && errorMessages.value.length)) {
      return errorMessages.value;
    } else if (props.hint && (props.persistentHint || props.focused)) {
      return [props.hint];
    } else {
      return wrapInArray(props.messages);
    }
  });

  const fileNames = computed(() => (model.value ?? []).map((file) => {
    const { name, size } = file;

    return !props.showSize ? name : `${name} (${humanReadableFileSize(size, base.value)})`;
  }));

  const isActive = computed(() => (isFocused.value || props.active || isDirty.value));
  const isDirty = computed(() => model.value.length || validationModel.value.length);

  const isValid = computed(() => {
    if (props.error || props.errorMessages?.length) {
      return false;
    } else if (!props.rules.length) {
      return true;
    } else if (isPristine.value) {
      return internalErrorMessages.value.length || validateOn.value.lazy ? null : true;
    } else {
      return !internalErrorMessages.value.length;
    }
  });

  const model = computed(() => {
    const files = [];

    if (props.modelValue?.length) {
      for (let i = 0; i < props.modelValue.length; i++) {
        const file = props.modelValue[i];
        files.push({ name: file.name ?? '', size: file.size ?? 0 });
      }
    }

    return files;
  });

  const totalBytesReadable = computed(() => humanReadableFileSize(totalBytes.value, base.value));
  const validationModel = computed({
    get() {
      const validationVal = wrapInArray(props.validationValue);

      if (validationVal.length) {
        return validationVal;
      } else {
        return model.value;
      }
    },
    set(val) {
      emit('update:validationValue', val);
    }
  });
  
  const validateOn = computed(() => {
    let value = props.validateOn || 'input';
    if (value === 'lazy') {
      value = 'input lazy';
    }
    const set = new Set(value?.split(' ') ?? []);

    return {
      blur: set.has('blur') || set.has('input'),
      input: set.has('input'),
      submit: set.has('submit'),
      lazy: set.has('lazy')
    };
  });

  watch(() => props.focused, () => {
    if (props.focused !== isFocused.value) {
      isFocused.value = props.focused;
    }
  });

  useToggleScope(() => validateOn.value.input, () => {
    watch(validationModel, () => {
      if (validationModel.value !== null) {
        validate();
      } else if (props.focused) {
        const unwatch = watch(() => props.focused, (val) => {
          if (!val) {
            validate();
          }

          unwatch();
        });
      }
    });
  });

  useToggleScope(() => validateOn.value.blur, () => {
    watch(() => props.focused, (val) => {
      if (!val) {
        validate();
      }
    });
  });

  function blur() {
    isFocused.value = false;
    emit('update:focused', isFocused.value);
  }

  function focus() {
    isFocused.value = true;
    emit('update:focused', isFocused.value);
  }

  function handleClick(event) {
    event.stopPropagation();

    if (props.readonly) {
      event.preventDefault();
    }

    onFocus();
  }
  
  function handleFileChange(event) {
    if (!event.target) {
      return;
    }

    totalBytes.value = 0;
    Object.keys(event.target.files).forEach((key) => {
      if (key !== 'length') {
        totalBytes.value += event.target.files[key].size;
      }
    });
    
    emit('update:modelValue', event.target.files);
  }

  function humanReadableFileSize(bytes, _base = 1000) {
    if (bytes < _base) {
      return `${bytes} B`;
    }

    const prefix = _base === 1024 ? ['Ki', 'Mi', 'Gi'] : ['k', 'M', 'G'];
    let unit = -1;
    while (Math.abs(bytes) >= _base && unit < prefix.length - 1) {
      bytes /= _base;
      ++unit;
    }
    return `${bytes.toFixed(1)} ${prefix[unit]}B`;
  }

  function onClickPrepend(e) {
    input.value.click();
  }

  function onClear(e) {
    e.stopPropagation();
    
    onFocus();

    nextTick(() => {
      emit('update:modelValue', undefined);

      emit('onClick:clear', e);
    });
  }

  function onControlClick(e) {
    input.value.click();

    emit('click:control', e);
  }

  function onControlMousedown(e) {
    emit('mousedown:control', e);
  }

  function onFocus() {
    if (input.value !== document.activeElement) {
      input.value.focus();
    }

    if (!isFocused.value) {
      focus();
    }
  }

  async function reset() {
    emit('update:modelValue', undefined);
    await nextTick();
    await resetValidation();
  }

  async function resetValidation() {
    isPristine.value = true;
    if (!validateOn.value.lazy) {
      await validate(true);
    } else {
      internalErrorMessages.value = [];
    }
  }

  async function validate(silent = false) {
    const results = [];

    isValidating.value = true;

    for (const rule of props.rules) {
      if (results.length >= Number(props.maxErrors ?? 1)) {
        break;
      }

      const handler = typeof rule === 'function' ? rule : () => rule;
      const result = await handler(validationModel.value);

      if (result !== true) {
        if (result !== false && typeof result !== 'string') {
          console.warn(`${result} is not a valid value. Rule functions must return boolean true or a string.`);
        } else {
          results.push(result || '');
        }
      }
    }

    internalErrorMessages.value = results;
    isValidating.value = false;
    isPristine.value = silent;

    emit('update:errors', internalErrorMessages.value);

    return internalErrorMessages.value;
  }

  onMounted(async () => {
    if (!validateOn.value.lazy) {
      await validate(true);
    }
  });
</script>

<style lang="scss" scoped>
  .file-input {
    --input-control-height: 56px;
    --input-padding-top: 16px;

    display: grid;
    width: 100%;
    flex: 1 1 auto;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    padding: 0;
    margin: 0;
    background-repeat: no-repeat;
    box-sizing: inherit;
    direction: ltr;
    grid-template-areas:
      "control"
      "messages";
    grid-template-columns: minmax(0, 1fr) max-content;
    grid-template-rows: auto auto;

    &.prepend {
      grid-template-areas:
        "prepend control"
        "a messages";
        grid-template-columns: max-content minmax(0, 1fr);
      
      &.append {
        grid-template-areas:
        "prepend control append"
        "a messages b";
        grid-template-columns: max-content minmax(0, 1fr) max-content;
      }
    }

    &:not(.prepend) {
      &.append {
        grid-template-areas:
        "control append"
        "messages b";
        grid-template-columns: minmax(0, 1fr) max-content;
      }
    }

    *, ::before, ::after {
      background-repeat: no-repeat;
      box-sizing: inherit;
      padding: 0;
      margin: 0;
    }

    ::before, ::after {
      text-decoration: inherit;
      vertical-align: inherit;
    }

    .prepend {
      display: flex;
      align-items: center;
      padding-top: 0;
      grid-area: prepend;
      margin-inline-end: 10px;
      cursor: pointer;
    }

    .append {
      display: flex;
      align-items: center;
      padding-top: 0;
      grid-area: append;
      margin-inline-start: 16px;
      cursor: pointer;
    }

    .control {
      display: flex;
      grid-area: var(--control-height);
      min-height: 2.75rem;
    }

    .details {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      grid-area: messages;
      padding-inline: 16px;
      padding-top: 6px;
      font-size: 0.75rem;
      font-weight: 400;
      letter-spacing: 0.0333333333em;
      line-height: normal;
      min-height: 22px;
      overflow: hidden;
      position: relative;

      .messages {
        flex: 1 1 auto;
        min-height: 14px;
        min-width: 1px;
        opacity: 0.7;
        font-size: 12px;
        position: relative;

        .message {
          word-break: break-word;
          overflow-wrap: break-word;
          word-wrap: break-word;
          hyphens: auto;
          line-height: 15px;
          transition-duration: 150ms;
        }
      }

      .messages-enter-active, .messages-leave-active {
        transition: all 150ms cubic-bezier(0.4, 0, 0.2, 1);
      }

      .messages-enter-from, .messages-leave-to {
        opacity: 0;
        transform: translateY(-75%);
      }

      .messages-leave-active {
        position: absolute;
      }

      .counter {
        position: absolute;
        right: 0.2rem;
        top: -75%;

        @media screen and (prefers-reduced-motion: no-preference) and (update: fast) {
          transition: 150ms top cubic-bezier(0.4, 0, 0.2, 1);
        }

        &.visible {
          top: 0.2rem;
        }
      }
    }

    .field-loader {
      width: 100%;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      border-bottom-left-radius: inherit;
      border-bottom-right-radius: inherit;
      overflow: hidden;
      position: absolute;
      top: calc(100% - 2px);
      left: 0;
      right: 0;
    }

    .field-content {
      display: flex;
      align-items: flex-start;
      grid-area: field;
      flex: 1 0;
      position: relative;
    }

    .field-label {
      display: block;
      min-width: 0;
      max-width: calc(100% - var(--field-padding-start) - var(--field-padding-end));
      contain: layout paint;
      margin-inline-start: var(--field-padding-start);
      margin-inline-end: var(--field-padding-end);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 1rem;
      color: inherit;
      letter-spacing: 0.009375em;
      opacity: 0.7;
      transform-origin: left center;
      transform: translateY(-50%);
      pointer-events: none;
      position: absolute;
      top: 50%;
      z-index: 1;

      @media screen and (prefers-reduced-motion: no-preference) and (update: fast) {
        transition: 0.15s cubic-bezier(0.4, 0, 0.2, 1);
        transition-property: opacity, transform;
      }

      &.floating {
        --field-label-scale: 0.75em;

        max-width: 100%;
        font-size: var(--field-label-scale);
        transform: none;
        top: 7px;
        visibility: hidden;
      }
    }

    input {
      height: 100%;
      width: 100%;
      background-color: transparent;
      border-radius: 0;
      border-style: none;
      font: inherit;
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      cursor: pointer;
    }

    .field-input {
      display: flex;
      align-items: center;
      column-gap: 2px;
      row-gap: 8px;
      min-height: m#{a}x(var(--input-control-height, 56px), 1.5rem + var(--field-input-padding-top) + var(--field-input-padding-bottom));
      min-width: 0;
      width: 100%;
      padding-inline: var(--field-padding-start) var(--field-padding-end);
      padding-top: var(--field-input-padding-top);
      padding-bottom: var(--field-input-padding-bottom);
      flex-wrap: wrap;
      letter-spacing: 0.009375em;
      color: inherit;
      opacity: 1;
      position: relative;

      .p-chip {
        font-size: 0.75rem;
        height: 25px;
        padding: 0 10px;
      }
    }

    .field-clearable {
      display: flex;
      align-items: center;
      grid-area: clear;
      padding-top: 0;
      margin-inline: 4px;
      overflow: hidden;
      opacity: 0;

      @media screen and (prefers-reduced-motion: no-preference) and (update: fast) {
        transition: .25s cubic-bezier(0.4, 0, 0.2, 1);
        transition-property: opacity;
      }
    }

    .field-prepend-inner {
      display: flex;
      align-items: center;
      grid-area: prepend-inner;
      padding-top: 0;
      padding-inline-end: var(--field-padding-before);
    }

    .field-append-inner {
      display: flex;
      align-items: center;
      grid-area: append-inner;
      padding-top: 0;
      padding-inline-start: var(--field-padding-after);
    }

    .field {
      --field-padding-start: 16px;
      --field-padding-end: 6px;
      --field-padding-top: 8px;
      --field-padding-bottom: 4px;
      --field-input-padding-top: calc(var(--field-padding-top, 8px) + var(--input-padding-top, 0));
      --field-input-padding-bottom: calc(var(--field-padding-bottom, 4px));

      display: grid;
      grid-template-areas: "field clear";
      grid-template-columns: minmax(0, 1fr) min-content;
      grid-area: control;
      max-width: 100%;
      flex: 1 0;
      contain: layout;
      direction: ltr;
      font-size: 16px;
      letter-spacing: 0.009375em;
      border-radius: 4px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      padding-inline-end: 12px;
      background: var(--surface-card);
      border: solid 0.1rem var(--surface-300);
      position: relative;

      &.prepend-inner {
        grid-template-areas: "prepend-inner field clear";
        grid-template-columns: min-content minmax(0, 1fr) min-content;

        &.append-inner {
          grid-template-areas: "prepend-inner field clear append-inner";
          grid-template-columns: min-content minmax(0, 1fr) min-content min-content;
        }
      }

      &:not(.prepend-inner) {
        &.append-inner {
          grid-template-areas: "field clear append-inner";
          grid-template-columns: minmax(0, 1fr) min-content min-content;
        }
      }
    }

    &:not(.disabled) {
      &:not(.error) {
        &:hover, &.focused, &:has(.prepend:hover), &:has(.append:hover) {
          .field {
            border-color: var(--surface-500);
          }
        }
      }

      &.focused, &.active {
        .field-label {
          visibility: hidden;

          &.floating {
            visibility: unset;
          }
        }
      }

      &.dirty {
        .field-clearable i {
          cursor: pointer;
        }

        &.focused, &:hover {
          .field-clearable {
            opacity: 1;
          }
        }
      }
    }

    &.disabled {
      .field {
        opacity: 0.4;
      }

      input {
        cursor: default;
      }
    }

    &.error {
      .field {
        border-color: var(--red-500);
      }

      .field-label, .messages, .prepend, .append, .field-prepend-inner, .field-append-inner, .field-clearable {
        color: var(--red-500);
      }
    }
  }
</style>
