<template>
  <Teleport v-if="modelValue" to="body">
    <div class="image-share">
      <div class="overlay" @click="close()" />
      <form :class="{ sent: sent }" @click="(e) => e.preventDefault()">
        <div class="header">
          <h2>{{ sent ? 'Images Uploaded' : 'Image Sharing' }}</h2>
          <i class="close-btn pi pi-times" @click="close()"/>
        </div>
        <span v-show="sent">You can close this modal now. Otherwise it will auto close in {{ timer }} seconds.</span>
        <div v-show="!sent" class="note">
          <span>Please Note: All submitted images are subject to curation by staff</span>
        </div>
        <div v-show="!sent" class="note">
          <span>
            If you do not have a share code from a booking,
            <router-link class="link" to="/contact-us">
              Contact Us.
            </router-link>
          </span>
        </div>
        <div v-show="!sent" class="form-item" id="item-sharecode">
          <div class="name">
            <span>Share Code</span>
            <small class="p-error" id="sharecode-error">{{ sharecodeError || '&nbsp;' }}</small>
          </div>
          <OtpInput v-model="sharecode" :length="22" inputClass="custom-otp-input" type="text" placeholder="" :conditionalClasses="otpConditionals" />
        </div>
        <div v-show="!sent" class="form-item" id="item-photographer">
          <div class="name">
            <span>Photographer</span>
            <small class="p-error" id="photographer-error">{{ authorError || '&nbsp;' }}</small>
          </div>
          <InputText v-model="author" id="photographer" class="input-text" :class="{ 'p-invalid': authorError }" placeholder="Enter your name" aria-describedby="photographer-error" />
        </div>
        <div v-show="!sent" class="form-item" id="item-images">
          <div class="name">
            <span>Images</span>
          </div>
          <FileInput v-model="images" id="images" name="images" multiple showSize counter label="Images" :rules="rules" :error="imagesError?.length > 0" :errorMessages="imagesError" @update:errors="handleErrors">
            <template #selection="{ fileNames }">
              <template v-for="(fileName, index) in fileNames" :key="fileName">
                <span v-if="index < 5">{{ fileName }}{{ ((index + 1) < images?.length && index < 4) ? ',' : '' }}</span>
                <span v-else-if="index === 5" class="text-overline mx-2">+{{ images?.length - 5 }} Image{{ (images?.length - 5) > 1 ? 's' : '' }}</span>
              </template>
            </template>
          </FileInput>
        </div>
        <div v-show="!sent" class="form-item" id="item-terms">
          <div class="name">
            <span>Please do not check this checkbox</span>
            <span class="required">*</span>
            <small class="p-error" id="terms-error">&nbsp;</small>
          </div>
          <input v-model="honeypot" type="checkbox" name="terms" id="terms" value="1" aria-describedby="terms-error" />
        </div>
        <Button v-show="!sent" label="Upload" :disabled="sharecode?.length !== 22 || images?.length === 0 || imagesErrors?.length || author === ''" @click="onSubmit" />
        <div v-show="!sent" class="recaptcha-note">
          <span>
            This site is protected by reCAPTCHA and the Google
            <a href="https://policies.google.com/privacy">Privacy Policy</a> and
            <a href="https://policies.google.com/terms">Terms of Service</a> apply.
          </span>
        </div>
      </form>
    </div>
  </Teleport>
</template>

<script setup>
  import { ref, defineProps, defineEmits, watch, onMounted } from 'vue';
  import { useReCaptcha } from 'vue-recaptcha-v3';
  import getHost from '@/utilities/GetHost';
  import FileInput from '@/components/inputs/FileInput.vue';
  import OtpInput from '@/components/inputs/OtpInput.vue';

  const { executeRecaptcha, recaptchaLoaded } = useReCaptcha();

  const props = defineProps({
    modelValue: {
      type: Boolean,
      default: false
    }
  });

  const emit = defineEmits(['update:modelValue']);

  const rules = [
    (value) => (!value || !value?.length || value.every((file) => file.size <= 5242880) || 'Image size should not exceed 5MB'),
    (value) => (!value || !value?.length || value?.length <= 5 || 'Only 5 images can be shared at a time')
  ];

  const otpConditionals = [
    'one', 'two', 'three', 'four', 'five', 'six', 'seven', 'eight', 'nine', 'ten', 'eleven',
    'twelve', 'thirteen', 'fourteen', 'fifteen', 'sixteen', 'seventeen', 'eighteen', 'nineteen', 'twenty', 'twenty-one', 'twenty-two'];

  const honeypot = ref();
  const honeypotTimer = ref(false);
  
  const author = ref('');
  const images = ref();
  const sharecode = ref('');
  const sent = ref(false);
  const timer = ref(15);
  const timerInterval = ref();
  
  const authorError = ref('');
  const imagesError = ref([]);
  const imagesErrors = ref([]);
  const sharecodeError = ref('');

  watch(() => props.modelValue, () => {
    if (props.modelValue) {
      honeypot.value = undefined;
      honeypotTimer.value = false;

      setTimeout(() => {
        honeypotTimer.value = true;

        if (document.querySelector('#terms')) {
          document.querySelector('#terms').tabIndex = -1;
          document.querySelector('#terms').autocomplete = 'off';
        }
      }, 5 * 1000);
    }
  });

  async function recaptcha() {
    await recaptchaLoaded();

    const token = await executeRecaptcha('share_images');

    return token;
  }

  const onSubmit = (event) => {
    event.stopPropagation();

    authorError.value = '';
    imagesError.value = [];
    sharecodeError.value = '';

    if (!author.value) {
      authorError.value = 'Photographer is required';
    }

    if (!images.value || !images.value?.length) {
      imagesError.value = ['Images is required'];
    }

    if (!sharecode.value) {
      sharecodeError.value = 'Share code is required';
    } else if (sharecode.value?.length !== 22) {
      sharecodeError.value = 'Invalid code length';
    } else if (/\W/.test(sharecode.value)) {
      sharecodeError.value = 'Special characters are not allowed';
    }
    
    if (!authorError.value && !imagesError.value?.length && !sharecodeError.value) {
      recaptcha().then((token) => {
        if (!honeypotTimer.value) {
          // Likely a bot, filled form too quickly, directly show success after random timeout without processing
          alert('Too quick');
        } else if (honeypot.value) {
          // Likely a bot, filled honeypot in, directly show success after random timeout without processing
          alert('Honeypot filled in');
        } else {
          const formData = new FormData();
          formData.append('recaptcha', token);
          formData.append('sharecode', sharecode.value);
          formData.append('author', author.value);

          for (let i = 0; i < images.value?.length; i++) {
            formData.append('images', images.value[i]);
          }

          // fetch(`${getHost()}/bookings/imageshare/upload`, { // TODO: Uncomment after demonstration build
          //   method: 'POST',
          //   body: formData
          // });
          // .then((res) => res.json()).then((result) => {
            // alert(JSON.stringify(result));
          // });
          
          alert(JSON.stringify(formData));
        }

        sent.value = true;
        timerInterval.value = setInterval(() => {
          timer.value -= 1;

          if (timer.value === 0) {
            clearInterval(timerInterval.value);
            close();
          }
        }, 1000);
        reset();
      });
    }
  };

  function reset() {
    // resetForm();
    author.value = '';
    images.value = undefined;
    sharecode.value = '';

    authorError.value = '';
    imagesError.value = [];
    sharecodeError.value = '';
  }

  function close() {
    clearInterval(timerInterval.value);
    emit('update:modelValue', false);
    
    setTimeout(() => {
      reset();
      timer.value = 15;
    }, 100);
  }

  function handleErrors(event) {
    imagesErrors.value = event;
  }

  onMounted(() => {
    if (props.modelValue) {
      honeypot.value = undefined;
      honeypotTimer.value = false;

      setTimeout(() => {
        honeypotTimer.value = true;

        if (document.querySelector('#terms')) {
          document.querySelector('#terms').tabIndex = -1;
          document.querySelector('#terms').autocomplete = 'off';
        }
      }, 5 * 1000);
    }
  });
</script>

<style lang="scss" scoped>
  .image-share {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: fixed !important;
    top: 0;
    left: 0;
    z-index: 25;
    color: var(--text-color);
    
    .overlay {
      width: 100%;
      height: 100%;
      background: transparentize(black, 0.05);
      position: absolute;
      top: 0;
      left: 0;
    }

    .link {
      text-decoration: none;
      color: var(--text-color);
      font-weight: 600;
      font-size: 0.8rem;

      &:hover {
        color: var(--theme-hover);
      }

      @media (hover: none) {
        color: var(--text-color) !important;
      }
    }

    form {
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
      width: 46.2%;
      min-height: 30%;
      background: var(--surface-card);
      padding: 2rem;
      border-radius: 0.3rem;
      z-index: 2;
      overflow-y: scroll;
      scrollbar-width: none;

      ::-webkit-scrollbar {
        width: 0px;
      }

      &.sent {
        align-items: center;
        
        span {
          margin-top: 10%;
          text-align: center;
        }
      }

      @media screen and (max-width: 700px) {
        width: 100%;
        height: 100%;
        border-radius: 0;
        padding: 1rem;

        &.sent span {
          margin-top: 75%;
        }
      }

      .header {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-bottom: 0.5rem;
        position: relative;

        h2 {
          margin: 0;
        }

        .close-btn {
          padding: 0.25rem;
          border-radius: 0.5rem;
          position: absolute;
          top: 0;
          right: 0;
          cursor: pointer;

          &:hover {
            color: var(--theme-hover);
          }
        }
      }

      .note {
        display: flex;
        align-items: center;
        align-self: flex-start;
        gap: 0.25rem;
        font-size: 0.75em;
      }

      :deep(.form-item) {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 0.5rem;

        @media screen and (max-width: 700px) {
          .otp-content {
            display: grid;
            grid-template-columns: repeat(11, 1fr);
          }
        }

        @media screen and (min-width: 701px) {
          .otp-content {
            &:has(.one:hover) .two {
              border-left-color: var(--surface-500);
            }

            &:has(.two:hover) .three {
              border-left-color: var(--surface-500);
            }

            &:has(.three:hover) .four {
              border-left-color: var(--surface-500);
            }

            &:has(.four:hover) .five {
              border-left-color: var(--surface-500);
            }

            &:has(.five:hover) .six {
              border-left-color: var(--surface-500);
            }

            &:has(.six:hover) .seven {
              border-left-color: var(--surface-500);
            }

            &:has(.seven:hover) .eight {
              border-left-color: var(--surface-500);
            }

            &:has(.eight:hover) .nine {
              border-left-color: var(--surface-500);
            }

            &:has(.nine:hover) .ten {
              border-left-color: var(--surface-500);
            }

            &:has(.ten:hover) .eleven {
              border-left-color: var(--surface-500);
            }

            &:has(.eleven:hover) .twelve {
              border-left-color: var(--surface-500);
            }

            &:has(.twelve:hover) .thirteen {
              border-left-color: var(--surface-500);
            }

            &:has(.thirteen:hover) .fourteen {
              border-left-color: var(--surface-500);
            }

            &:has(.fourteen:hover) .fifteen {
              border-left-color: var(--surface-500);
            }

            &:has(.fifteen:hover) .sixteen {
              border-left-color: var(--surface-500);
            }

            &:has(.sixteen:hover) .seventeen {
              border-left-color: var(--surface-500);
            }

            &:has(.seventeen:hover) .eighteen {
              border-left-color: var(--surface-500);
            }

            &:has(.eighteen:hover) .nineteen {
              border-left-color: var(--surface-500);
            }

            &:has(.nineteen:hover) .twenty {
              border-left-color: var(--surface-500);
            }

            &:has(.twenty:hover) .twenty-one {
              border-left-color: var(--surface-500);
            }

            &:has(.twenty-one:hover) .twenty-two {
              border-left-color: var(--surface-500);
            }
          }
        }

        .custom-otp-input {
          width: 2.285rem;
          height: 2.81rem;
          font-size: 24px;
          appearance: none;
          text-align: center;
          transition: all 0.2s;
          border-radius: 0;
          border: 0.1rem solid var(--surface-300);
          border-right: 0 none;
          background: var(--surface-card);
          outline-offset: -2px;
          outline-color: transparent;
          transition: outline-color 0.3s;
          color: var(--text-color);

          &::placeholder {
            font-size: 18px;
          }
          
          &.one {
            border-top-left-radius: 4px;
            border-bottom-left-radius: 4px;
          }
          
          &.twenty-two {
            border-top-right-radius: 4px;
            border-bottom-right-radius: 4px;
            border-right: 0.1rem solid var(--surface-300);
          }
          
          &:hover {
            border-color: var(--surface-500);
          }
    
          &:focus {
            outline: 2px solid var(--primary-color);
          }

          @media (hover: none) {
            border-color: var(--surface-300) !important;
          }
    
          @media screen and (max-width: 700px) {
            width: 1.85rem;
    
            &.one, &.two, &.three, &.four, &.five, &.six, &.seven, &.eight, &.nine, &.ten, &.eleven {
              border-bottom: none;
            }
            
            &.one {
              border-bottom-left-radius: 0;
            }
    
            &.twenty-two {
              border-top-right-radius: 0;
            }
    
            &.eleven {
              border-top-right-radius: 4px;
              border-right: 0.1rem solid var(--surface-300);
            }
    
            &.twelve {
              border-bottom-left-radius: 4px;
            }
          }
        }

        .name {
          display: flex;
          align-items: center;
          gap: 0.25em;
          width: 100%;
          font-size: 0.95em;

          small {
            margin-left: auto;
            margin-right: 0;
          }
        }
        
        .p-component {
          &:not(.p-button) {
            width: 100% !important;
          }
        }

        .p-dropdown-label {
          text-align: left;
        }
      }

      #item-terms {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        opacity: 0;
      }

      .required {
        color: var(--red-500);
      }

      .recaptcha-note {
        display: flex;
        gap: 0.3em;
        font-size: 0.8em;
        margin-bottom: 1.5em;

        a {
          text-decoration: none;
          color: var(--text-color);
          font-weight: 600;
          font-size: 0.85rem;

          &:hover {
            color: var(--theme-hover);
          }

          @media (hover: none) {
            color: var(--text-color) !important;
          }
        }
      }
    }

    .input-code {
      display: flex;
      align-items: center;
      gap: 0;
      width: 100%;

      @media screen and (max-width: 700px) {
        display: grid;
        grid-template-columns: repeat(11, 1fr);
      }
    }

    .input-text {
      width: 100%;
    }

    .custom-otp-input {
      width: 2.285rem;
      height: 2.8rem;
      font-size: 24px;
      appearance: none;
      text-align: center;
      transition: all 0.2s;
      border-radius: 0;
      border: 0.1rem solid var(--surface-300);
      border-right: 0 none;
      background: var(--surface-card);
      outline-offset: -2px;
      outline-color: transparent;
      transition: outline-color 0.3s;
      color: var(--text-color);
      
      &:first-child {
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
      }
      
      &:last-child {
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        border-right: 0.1rem solid var(--surface-300);
      }
      
      &:hover {
        border-color: var(--surface-500);
      }

      &:focus {
        outline: 2px solid var(--primary-color);
      }

      @media screen and (max-width: 700px) {
        width: 1.85rem;

        &:nth-child(-n+11) {
          border-bottom: none;
        }
        
        &:first-child {
          border-bottom-left-radius: 0;
        }

        &:last-child {
          border-top-right-radius: 0;
        }

        &:nth-child(11) {
          border-top-right-radius: 4px;
          border-right: 0.1rem solid var(--surface-300);
        }

        &:nth-child(12) {
          border-bottom-left-radius: 4px;
        }
      }
    }
  }
</style>
