export function toKebabCase(str = '') {
  const kebab = str
    .replace(/[^a-z]/gi, '-')
    .replace(/\B([A-Z])/g, '-$1')
    .toLowerCase();
  return kebab;
}

export function useToggleScope(source, fn) {
  let scope;
  function start() {
    scope = effectScope();
    scope.run(() => (fn.length
      // eslint-disable-next-line no-unused-expressions
      ? fn(() => { scope?.stop(); start(); })
      : (fn)()));
  }

  watch(source, (active) => {
    if (active && !scope) {
      start();
    } else if (!active) {
      // eslint-disable-next-line no-unused-expressions
      scope?.stop();
      scope = undefined;
    }
  }, { immediate: true });

  onScopeDispose(() => {
    // eslint-disable-next-line no-unused-expressions
    scope?.stop();
  });
}

