<template>
  <main class="gallery">
    <div class="gallery-scroll">
      <div class="gallery-content">
        <div class="header">
          <h1>Gallery</h1>
          <div class="share">
            <span>Do you have photos you wish to share?</span>
            <div class="btn" :class="{ disabled: !sharingEnabled }" @click="share">
              <i class="pi pi-upload" />
              <span>Upload</span>
              <Tooltip :text="sharingEnabled ? 'Upload Photos' : 'Image upload is currently unavailable, please try again later'" />
            </div>
          </div>
        </div>
        <div class="container">
          <div v-for="(image, index) in images" :key="`img${index}`" class="gallery-item">
            <ExpandableImage class="item-content" :title="image.author" :src="image.src" dismissableMask closeOnEscape @loaded="autoGrid" />
            <!-- <div class="item-content">
              <i class="btn pi pi-external-link" @click="expandImage(image)">
                <Tooltip text="Expand Image" />
              </i>
              <img :alt="image.author" :src="image.src" @load="autoGrid" />
              <span class="author">{{ image.author }}</span>
            </div> -->
          </div>
        </div>
      </div>
      <Footer />
    </div>
    <ImageSharing v-model="sharingOpen" />
  </main>
</template>

<script setup>
  import { ref, onBeforeMount, onMounted } from 'vue';
  import fetchPlus from '@/utilities/FetchPlus';
  import getHost from '@/utilities/GetHost';
  import Footer from '@/components/core/Footer.vue';
  import ExpandableImage from '@/components/common/ExpandableImage.vue';
  import ImageSharing from '@/components/gallery/ImageSharing.vue';

  // TODO: remove after demonstration
  import galleryImages from '@/assets/demonstration/gallery/list.json';

  const images = ref([]);
  const sharingOpen = ref(false);
  const sharingEnabled = ref(false);

  function share() {
    sharingOpen.value = !sharingOpen.value;
  }

  function getVal(elem, style) {
    return parseInt(window.getComputedStyle(elem).getPropertyValue(style));
  }

  function getHeight(elem) {
    return elem.querySelector('.item-content').getBoundingClientRect().height;
  }

  function getChildren(elem) {
    const { childNodes } = elem;
    const children = [];
    let i = childNodes.length;

    while (i--) {
      if (childNodes[i].nodeType === 1) {
        children.unshift(childNodes[i]);
      }
    }

    return children;
  }

  function autoGrid() {
    const container = document.querySelector('.container');
    if (container) {
      container.querySelectorAll('.gallery-item').forEach((item) => {
        const img = item.querySelector('img');
        if (img.complete) {
          const altura = getVal(container, 'grid-auto-rows');
          const gap = getVal(container, 'grid-gap');
          item.style.gridRowEnd = `span ${Math.ceil((getHeight(item) + gap) / (altura + gap))}`;
        } else {
          getChildren(item.querySelector('.item-content')).forEach((child) => {
            child.classList.add('byebye');
          });

          img.addEventListener('load', () => {
            const altura = getVal(container, 'grid-auto-rows');
            const gap = getVal(container, 'grid-gap');
            item.style.gridRowEnd = `span ${Math.ceil((getHeight(item) + gap) / (altura + gap))}`;
            getChildren(item.querySelector('.item-content')).forEach((child) => {
              child.classList.remove('byebye');
            });
          });
        }
      });
    }
  }

  onBeforeMount(() => {
    const getShuffledArr = (arr) => {
      const newArr = arr.slice();
      for (let i = newArr.length - 1; i > 0; i--) {
        const rand = Math.floor(Math.random() * (i + 1));
        [newArr[i], newArr[rand]] = [newArr[rand], newArr[i]];
      }
      return newArr;
    };

    // fetchPlus(`${getHost()}/data/gallery/list`, 15).then((data) => { // TODO: Uncomment after demonstration
    //   data.forEach((item) => {
    //     item.src = `${getHost()}/data/gallery/image/${item.filename}`;
    //   });
      
    //   images.value = getShuffledArr(data);
    //   // images.value = data;
    // }).catch(() => {
    //   console.error('Failed to fetch images');
    // });

    // fetchPlus(`${getHost()}/bookings/imageshare/available`, 15).then((data) => { // TODO: Uncomment after demonstration
    //   sharingEnabled.value = data.available;
    // }).catch(() => {
    //   console.error('Failed to find imageshare status, defaulting to disabled');
    //   sharingEnabled.value = false;
    // });

    setTimeout(() => { // TODO: Remove after demonstration
      const files = galleryImages.files;
      const data = [];

      for (let i = 0; i < files.length; i++) {
        // eslint-disable-next-line import/no-dynamic-require, global-require, @typescript-eslint/no-var-requires
        const metadata = require(`@/assets/demonstration/gallery/metadata/${files[i]}.json`);
        // eslint-disable-next-line import/no-dynamic-require, global-require, @typescript-eslint/no-var-requires
        metadata.src = require(`@/assets/demonstration/gallery/images/${files[i]}.png`);
        data.push(metadata);
      }

      images.value = getShuffledArr(data);
    }, Math.floor(Math.random() * (7500 - 801) + 800));
  });

  onMounted(() => {
    autoGrid();

    const container = document.querySelector('.container');
    window.addEventListener('resize', () => {
      const altura = getVal(container, 'grid-auto-rows');
      const gap = getVal(container, 'grid-gap');
      container.querySelectorAll('.gallery-item').forEach((item) => {
        item.style.gridRowEnd = `span ${Math.ceil((getHeight(item) + gap) / (altura + gap))}`;
      });
    });
  });
</script>

